<template>
    <div 
        id="cp-navbar">
        
        <nav class="navbar navbar-expand-md fixed-top bg-light">
            <a class="navbar-brand" href="#">{{ data.app.name }}</a>
            <button 
                class="navbar-toggler" 
                type="button" 
                data-toggle="collapse" 
                data-target="#navbarComponent" 
                aria-controls="navbarComponent" 
                aria-expanded="false" 
                aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>

            <div class="collapse navbar-collapse" id="navbarComponent">
                <form class="form-inline my-2 my-lg-0 ml-auto">
                    <input 
                        ref="lattes_url_or_id"
                        class="form-control mr-sm-2" 
                        type="text" 
                        placeholder="Lattes URL or ID" 
                        aria-label="Search">
                    <button 
                        @click.stop.prevent="resquestUserData()"
                        class="btn btn-secondary my-2 my-sm-0 item"
                        type="submit">
                        <span aria-hidden="true" class="icon-eyeglasses"></span>
                    </button>
                </form>
            </div>
        </nav>

    </div>
</template>

<script>
    import axios from 'axios';

    export default {
        components: {
        },
        props: {
            data: {
                type: Object,
                required: true
            }
        },
        methods: {
            resquestUserData() {

                //
                let param = this.$refs.lattes_url_or_id.value
                
                //
                let url = 'http://localhost:5000/'+param;

                axios.get(url).then(res => { 
                    this.data.user = {
                        name: res.data.name,
                        references: res.data.reference,
                        academics: res.data.academics
                    }
                });

            }
        }
    }
</script>

<style>
</style>


