<template>
    <div id="app-client-index">

        <Navbar :data="data"></Navbar>

        <main role="main">

            <!-- Main jumbotron for a primary marketing message or call to action -->
            <div class="jumbotron">
                <div class="container">
                    <h1 class="display-3">{{ data.user.name }}</h1>
                    <h4>{{ data.user.references }}</h4>
                    <div class="academics" v-html="data.user.academics">{{ data.user.academics }}</div>
                </div>
            </div>

        </main>

        <!-- Index Footer -->
        <Footer :data="data"></Footer>

    </div>
</template>

<script>
    import Navbar from '../../components/Navbar.vue'
    import Footer from '../../components/Footer.vue'

    export default {
        name: "Index",
        components: {
            Navbar,
            Footer,
        },
        props: {
            data: {
                type: Object,
                required: true
            }
        },
        methods: {}
    }
</script>

<style>
</style>


